:root {
  --indent: 20px;
  --header-height: 100px;
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
  line-height: 1.3;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  background-image: url(../../img/bg-hero.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  text-size-adjust: 100%;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  transition: opacity 0.25s ease-in-out;
}

a:hover {
  opacity: 0.8;
}

a:active {
  opacity: 0.6;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}

.container {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: var(--indent);
  box-sizing: border-box;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--header-height);
  color: #fff;
}

.header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: #171717;
}

.header__container {
  display: flex;
  align-items: center;
}

.header__link {
  margin-left: auto;
}

.logo {
  display: block;
}

.logo img {
  display: block;
}

.icon-link {
  display: inline-flex;
  align-items: center;
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
}

.icon-link::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  margin-right: var(--indent);
  background-image: linear-gradient(135deg, #3e1f05, #6a4e26, #c29c63, #d4b383);
  mask-position: center;
  mask-size: contain;
  mask-repeat: no-repeat;
}

.icon-link--telegramm::before {
  mask-image: url(../../img/telegram.svg);
}

.hero {
  position: relative;
  padding-top: var(--header-height);
}

.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(../../img/hero-bg.jpg);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}

.hero__title {
  position: relative;
  width: 290px;
  height: 290px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  text-align: center;
  padding: var(--indent);
  box-sizing: border-box;
  text-shadow: 0 0 10px #fff;
}

.hero__title::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120%;
  height: 120%;
  background-image: url(../../img/hero-img.png);
  background-size: contain;
  z-index: -1;
}

.title {
  font-size: 1.5em;
  text-transform: uppercase;
}